import Topbar from '../../components/topBar/topbarx.vue'
export default{
	data(){
		return{
			title: '换绑手机号',
			phone: '18302824291',
			yzCode: '', 
		}
	},
	onLoad() {
		
	},
	methods:{
		// 获取验证码
		yzClick(){
			let _this = this
			if(!this.phone) {
				uni.showModal({
					title: '提示',
					content: '请输入手机号'
				})
				return
			}
			uni.showLoading({
				title: '正在获取验证码...'
			})
			uni.request({
				url: this.$http + '/api/sms/sendSms',
				method: 'POST',
				data:{
					mobile: _this.phone,
					event: 'changemobile'
				},
				success(res){
					uni.hideLoading()
				}
			})
		},
		// 保存
		subClick(){
			if(!this.phone) {
				uni.showModal({
					title: '提示',
					content: '请输入手机号'
				})
				return
			}
				
			if(!this.yzCode){
				uni.showModal({
					title: '提示',
					content: '请输入验证码'
				})
				return
			}
			uni.request({
				url: this.$http + '/api/user/changemobile',
				method: 'POST',
				data:{
					token: this.$store.state.userInfo.token,
					mobile: this.phone,
					captcha: this.yzCode
				},
				success(res){
					if(res.data.code == 1){
						uni.showToast({
							title: '修改成功'
						})
					}
				}
			})
			
			
		}
	},
	components: {
		Topbar
	}
}
	
